<template>
    <v-btn @click="onClick" :width="size" :height="size - 20" :title="hint" :color="color" :disabled="disabled" :depressed="depressed" class="x-short-btn">
        <div class="d-flex flex-column">
            <v-icon v-if="!image" v-text="icon" :size="size - 50" class="x-icon"></v-icon>
            <v-img v-else :src="image" :height="size - 50" contain></v-img>
            <div v-text="label" class="x-label text-caption text-capitalize"></div>
        </div>
    </v-btn>
</template>

<script>
export default {
    name: 'ShortBtn',
    props: {
        label: {
            type: String,
            default: 'Label'
        },
        hint: {
            type: String,
        },
        icon: {
            type: String,
            default: 'mdi-home'
        },
        image: {
            type: String
        },
        disabled: {
            type: Boolean,
            default: false
        },
        color: {
            type: String,
            default: 'primary'
        },
        size: {
            type: [String, Number],
            default: 80
        },
        depressed: {
            type: Boolean,
            default: true
        },
        to: {
            type: [Object, String]
        },
    },
    methods: {
        onClick($event) {
            this.$emit('click', $event);

            if (this.to) {
                if (typeof this.to === 'object') {
                    return this.$router.push(this.to);
                }
                if (this.to.indexOf('/') > -1) {
                    return this.$router.push({ path: this.to });
                }
                this.$router.push({ name: this.to });
            }
        },
    },
}
</script>

<style scoped>
.x-short-btn .x-label{
    font-size: 0.72rem !important;
}
</style>
